import React, { Component } from 'react';
import './App.css';
import Navigation from './Navigation/Navigation';
import Home from './Home/Home';
import Project from './Project/Project';
import Contact from './Contact/Contact';

class App extends Component {
  render() {
    let iconPhotos = [
      {
        icons : "facebook.png",
        link : "https://web.facebook.com/RobertoKeane96"
      },
      {
        icons : "github.png",
        link : "https://github.com/roberto-keane"
      },
      {
        icons : "linkedin.png",
        link : "https://www.linkedin.com/in/roberto-keane/"
      },
    ];

    let fetchIcons = iconPhotos.map((icon, i) => {
      return <a href={icon.link} target="_blank" rel="noopener noreferrer" key={i}><img className="social-media" key={i} alt="loading" src={require(`./img/${icon.icons}`)} /></a>
    })

    return (
      <div>
        <Navigation />
        <Home />
        <Project />
        <Contact fetchIcons={fetchIcons}/>        
      </div>
    );
  }
}

export default App;
