import React, { Component } from 'react';
import './Project.css';
import pImage1 from '../img/robofriends.png';
import pImage2 from '../img/faceRecognition.png';

class Project extends Component {   
    render( ) { 

        return ( 
            <div>
                <div className="zone project width" id="project">
                    <div className="box-container">

                        <h2>My Project</h2>
                        <span>Here's some project that I put in github..</span>

                        <div className="grid-wrapper">
                        
                            <div className="box zone">
                                <a href="https://github.com/roberto-keane/RoboFriends" target="_blank" rel="noopener noreferrer">
                                    <img src={pImage1} alt="project"/>
                                </a>
                            </div>

                            <div className="box zone">
                                <a href="https://github.com/roberto-keane/RoboFriends" target="_blank" rel="noopener noreferrer">
                                    <img src={pImage2} alt="project"/>
                                </a>                              
                            </div>   

                        </div>

                        <h1>COMMING SOON</h1>

                    </div>
                    
                </div>
            </div> 
        );
    }
}
 
export default Project;